@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@400;700&family=Zen+Dots&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

#logo {
    font-family: 'Zen Dots', cursive;
    fill: rgba(255, 255, 255, 1);
    position: relative;


    text {
        text-transform: uppercase;
        font-size: 5rem;
        stroke-width: 2;
        stroke: rgba(255, 255, 255, 1);
        stroke-dasharray: 310;
        stroke-dashoffset: 0;

        animation: stroke 5s linear;


    }

    @keyframes stroke {

        0% {
            stroke-dashoffset: 0;
            fill: rgba(255, 255, 255, 0);
        }

        40% {
            stroke-dashoffset: 310;
            fill: rgba(255, 255, 255, 0.3);
        }

        80% {
            stroke-dashoffset: 620;
            fill: rgba(255, 255, 255, 0.6);
        }

        100% {
            stroke-dashoffset: 620;
            fill: rgba(255, 255, 255, 1);

        }
    }

}

.h2 {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 4rem;
    color: white;

    @media (max-width: 768px) {
        font-size: 2rem;
    }
}

.listItem {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 2rem;
    letter-spacing: 1.5px;
    line-height: 1.7;
    color: white;

    &:hover {
        text-decoration: underline;
    }

    &:nth-child(1) {
        font-size: 2.2rem;
    }

    &:nth-child(5) {
        font-size: 2.2rem;
    }

    &:nth-child(n + 2):nth-child(-n + 4) {
        margin-left: 1rem;
    }

    &:nth-child(6) {
        margin-left: 1rem;
    }

    &:nth-child(7) {
        font-size: 1.8rem;
        margin-left: 2rem;
    }

    &:nth-child(8) {
        margin-left: 1rem;
    }

    &:nth-child(9) {
        font-size: 1.8rem;
        margin-left: 2rem;
    }


    &:nth-child(n + 10):nth-child(-n + 13) {
        margin-left: 1rem;
    }

    span {
        cursor: pointer;
    }

    @media (max-width: 768px) {
        margin: 0;
        font-size: 1.5rem;
    }

}

.active {
    color: orange;
}